  border-radius: 1.5em;
  position: relative;
  transition: background-color .3s;
}

.switch-label::after {
  content: '';
  display: block;
  position: absolute;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  background: white;
  left: 0;
  top: 0;
